import React from 'react';
import CardsSection from './CardsSection';
import {targeta} from '../../Api/inicioCards';
import fondo from '../../medias/images/desktop-1245714.jpg';
import TableSection from './TableSection';
import Grafica from './Grafica';
import nota from '../../medias/images/notes.png';
import {useAnimator} from '../../hooks/useAnimator';
import contenido from '../../medias/images/content.png';
import TituloApartados from './TituloApartados';
import SectionContent from './SectionContent';
import {ReferenciasT,ReferenciasB} from './Referencias';
import Fondo from './Fondo';
import Caruesel from './Carusel';
const Section = ()=>{
    useAnimator(".an",-960);
    return(
        <>
        <section class="hero" style={{textAlign:"center"}}>
            <div class="hero-body">
                <TituloApartados titulo="Contenido del curso" img={contenido}/>
                <hr/>
                <div class="columns is-desktop">
                    {targeta.map((el)=>
                        (
                            <div class="column">
                                <CardsSection  imagen={el.img}
                                    titulo={el.title} 
                                    descripcion={el.descripcion}
                                    horasTotales={el.horasTotales}
                                  />
                            </div>
                        )
                    )}
                </div>
                <hr/>
                <Fondo fondo={fondo} mensaje={<>
                                    <span>Recuerda</span>
                                    <br/>
                                    <span>El que tiene fé en si mismo</span>
                                    <br/>
                                    <span>no necesita que los demas crean en el</span>
                                    <br/>
                                    <span>@Miguel de Unamundo</span></>}
                />
               <hr/>
                <div class="columns">
                    <div class="column" 
                        style={{textAlign:"justify"
                        ,paddingLeft:"25vh"
                        ,paddingRight:"25vh"
                        ,fontSize:"3.3vh"
                        ,background:"linear-gradient(90deg, rgba(2,0,36,1) 0%, rgba(9,9,121,1) 35%, rgba(0,212,255,1) 100%)"
                        ,marginLeft:"-1.5em"
                        ,marginRight:"-1.5em"
                        ,color:"white"
                        ,paddingTop:"10vh"
                        ,paddingBottom:"10vh"}}>
                        <b><img src={nota}/>Importante</b>
                        <p>Para poder seguir este curso sin ningun problema se recomienda tener conocimientos basicos en 
                        los lenguajes de programación usados, esto debido a que no se abordaran temas en si, si no que se 
                        aplicaran los conceptos al desarrollo web todas las herramientas en conjunto no una por una si desea
                        aprender estos lenguajes desde cero dejo unas referencias a los que considero son unos exelentes profesores.
                        </p>

                        <ol style={{textAlign:"left",listStyle:"none"}}>
                            <li>
                                <a href="https://youtube.com/playlist?list=PLU8oAlHdN5BktAXdEVCLUYzvDyqRQJ2lk" rel="noopener" target="_blank">
                                    Aprende java desde cero @Juan - pildorasinformaticas
                                </a>
                            </li>
                            <li>
                                <a href="https://www.youtube.com/playlist?list=PLvq-jIkSeTUZ6QgYYO3MwG9EMqC-KoLXA" rel="noopener" target="_blank">
                                    Aprende Javascript desde cero @Jhon - Jhonmircha
                                </a>
                            </li>
                        </ol>
                    </div>
                </div>
                <hr/>
                <SectionContent titulo1="Lenguajes más usados del 2021"content1={<Grafica/>} content2={<TableSection/>}/>
                <hr/>
                <TituloApartados titulo="Noticias" img={contenido}/>
                <hr/>
                <SectionContent content1="hola"/>
            </div>
            <ReferenciasT/>
            <ReferenciasB/>
        </section>
        </>
    );
}
export default Section;