import React from 'react';

const TituloApartados = ({titulo,img})=>{
    return(
        <div style={{marginLeft:"-1.5em"
        ,marginRight:"-1.5em"
        ,background:"linear-gradient(90deg, rgba(2,0,36,1) 0%, rgba(9,9,121,1) 35%, rgba(0,212,255,1) 100%)"
        ,paddingTop:"5vh"
        ,paddingBottom:"5vh"}}>
        <img src={img} />
        {"\u00a0"}
        <p class="title" style={{color:"white"}}>{titulo}</p>
    </div>
    );
}
export default TituloApartados;